import "../App.css";
import ParcialmenteNubladoYNeblinoso from '../img/ParcialmenteNubladoYNeblinoso.svg';

function DiaYHorario({time, cargando}) {
    //Obtengo fecha de api.json
    const fecha = new Date(time);
    //Defino los nombres de los das de la semana
    const diasSemana = ["Domingo", "Lunes", "Martes", "Mircoles", "Jueves", "Viernes", "Sbado"];
    //Obtengo el nmero del da de la semana (0 = Domingo, 1 = Lunes, etc.)
    const numeroDiaSemana = fecha.getDay();    
    //Obtengo el nombre del da de la semana
    const nombreDiaSemana = diasSemana[numeroDiaSemana];
    //Obtengo la hora y minutos
    const hora = fecha.getHours();
    const minutos = fecha.getMinutes();

    if (cargando) {
        return (
              <h2>Cargando</h2>
        )
  } else {
    return (
        <div className='daYHorario'>
            <img className='sticker' src={ParcialmenteNubladoYNeblinoso} alt="parcialmente nublado y neblinoso"></img>
            <div className='daYHorarioTexto'> {nombreDiaSemana}, {hora}:{minutos < 10 ? '0' : ''}{minutos}</div>
        </div>
    );
  } 
}

export default DiaYHorario;